.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  padding-top: 38px;
  padding-bottom: 34px;

  @media @tablet {
    box-sizing: border-box;
    width: 100%;
    flex-direction: row;
    justify-content: space-between;
    padding-left: 60px;
    padding-right: 60px;
  }

  @media @desktop {
    padding-left: 119px;
    padding-right: 118px;
  }
}

.footer__visit-tag {
  .heading(middle);
  margin: 0;
  margin-bottom: 34px;

  @media @tablet {
    margin-bottom: 0;
  }
}

.footer__social {
  margin: 0;
  margin-bottom: 39px;

  @media @tablet {
    margin-bottom: 0;
  }
}

