.form {
  display: grid;

  @media @tablet {
    grid-template-columns: repeat(2, 1fr);
  }
}

.form__item {
  padding: 24px 20px 21px 20px; //23px->21
  margin: 0;
  border: none;
  border-top: 1px solid #e5e5e5;
  width: 100%;
  box-sizing: border-box;

  @media @tablet {
    grid-column: 1/-1;
    border: none;
    padding-top: 0;

    &--introduction {
      grid-column: 1/2;
      border-right: 1px solid #e5e5e5;
    }

    &--contacts {
      grid-column: 2/3;
    }
  }

  &--impression {
    display: grid;
    justify-items: start;

    @media @tablet {
      grid-template-columns: repeat(2, 1fr);
    }

    @media @desktop {
      grid-template-columns: repeat(3, 1fr);
    }
  }

  &--places {
    display: grid;
    justify-items: start;

    @media @tablet {
      grid-template-columns: repeat(2, 1fr);
    }

    @media @desktop {
      grid-template-columns: repeat(4, 1fr);
    }
  }
}

.form__legend {
  .heading();
  display: block;
  box-sizing: border-box;
  width: 100%;
  text-align: center;
  float: left;
  clear: both;
  padding: 0;
  margin-bottom: 19px;

  @media @tablet {
    .heading(middle);
    padding-left: 60px;
    grid-column: 1/-1;
  }
}

// .form__item--introduction .form__legend {
//   @media @tablet {
//     .heading(middle);
//     padding-left: 60px;
//   }
// }

.form__item--contacts .form__legend {
  @media @tablet {
    .heading(middle);
    text-align: left;
    padding-left: 5px;
  }
}

.form__input {
  display: block;
  margin-bottom: 15px;

  & span {
    padding-left: 16px;
  }
}


.form__item--introduction .form__input {
  @media @tablet {
    padding-right: 23px;
    padding-left: 40px;
  }

  @media @desktop {
    padding-right: 24px;
  }
}

.form__item--contacts .form__input {
  @media @tablet {
    padding-left: 24px;
    padding-right: 41px;
  }

  @media @desktop {
    padding-left: 63px;
  }
}

.form__comment {
  box-sizing: border-box;
  background-color: #f2f2f2;
  width: 100%;
  height: 150px;
  margin-bottom: 30px;
  padding: 16px;
  font-family: inherit;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;

  &::placeholder {
    color: @bg-color-modal;
  }
}

.form__submit {
  .heading(middle);
  padding: 15px;
  margin: 0 20px;
}

.form__note {
  text-align: right;
  margin-bottom: 18px;
  padding: 0 20px;
}
